"use client"

import { useState, useEffect } from 'react'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Navigation } from "@/components/navigation"
import { Footer } from "@/components/footer"
import Link from 'next/link'

interface Restaurant {
  id: number
  name: string
  description: string
  image_url: string
  rating: number
  review_count: number
  cuisine_type: string
  delivery_time_min: number
  delivery_time_max: number
  delivery_fee: number
  minimum_order: number
  is_sponsored: boolean
  is_active: boolean
}

export default function RestaurantsPage() {
  const [restaurants, setRestaurants] = useState<Restaurant[]>([])
  const [filteredRestaurants, setFilteredRestaurants] = useState<Restaurant[]>([])
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedCuisine, setSelectedCuisine] = useState('all')
  const [sortBy, setSortBy] = useState('rating')
  const [language, setLanguage] = useState('en')
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Get user language preference
    const userData = localStorage.getItem('user')
    if (userData) {
      try {
        const user = JSON.parse(userData)
        setLanguage(user.language || 'en')
      } catch (error) {
        // Ignore error
      }
    }

    // Mock data for now (will be replaced with API call)
    const mockRestaurants: Restaurant[] = [
      {
        id: 1,
        name: "Burger Palace",
        description: "Home of the best burgers in town",
        image_url: "https://images.pexels.com/photos/1639557/pexels-photo-1639557.jpeg",
        rating: 4.5,
        review_count: 1000,
        cuisine_type: "Burgers",
        delivery_time_min: 20,
        delivery_time_max: 35,
        delivery_fee: 2.50,
        minimum_order: 10.00,
        is_sponsored: true,
        is_active: true
      },
      {
        id: 2,
        name: "Pizza Heaven",
        description: "Authentic Italian pizzas made fresh",
        image_url: "https://images.pexels.com/photos/825661/pexels-photo-825661.jpeg",
        rating: 4.3,
        review_count: 500,
        cuisine_type: "Pizza",
        delivery_time_min: 30,
        delivery_time_max: 45,
        delivery_fee: 3.00,
        minimum_order: 15.00,
        is_sponsored: false,
        is_active: true
      },
      {
        id: 3,
        name: "Sushi Master",
        description: "Fresh sushi and Japanese delicacies",
        image_url: "https://images.pexels.com/photos/2098085/pexels-photo-2098085.jpeg",
        rating: 4.7,
        review_count: 2000,
        cuisine_type: "Japanese",
        delivery_time_min: 25,
        delivery_time_max: 40,
        delivery_fee: 4.00,
        minimum_order: 20.00,
        is_sponsored: true,
        is_active: true
      },
      {
        id: 4,
        name: "Mama's Kitchen",
        description: "Traditional Kenyan dishes",
        image_url: "https://images.pexels.com/photos/1640777/pexels-photo-1640777.jpeg",
        rating: 4.6,
        review_count: 800,
        cuisine_type: "Kenyan",
        delivery_time_min: 15,
        delivery_time_max: 30,
        delivery_fee: 2.00,
        minimum_order: 8.00,
        is_sponsored: false,
        is_active: true
      },
      {
        id: 5,
        name: "Spice Garden",
        description: "Authentic Indian cuisine",
        image_url: "https://images.pexels.com/photos/1633578/pexels-photo-1633578.jpeg",
        rating: 4.4,
        review_count: 600,
        cuisine_type: "Indian",
        delivery_time_min: 25,
        delivery_time_max: 40,
        delivery_fee: 3.50,
        minimum_order: 12.00,
        is_sponsored: false,
        is_active: true
      }
    ]

    setRestaurants(mockRestaurants)
    setFilteredRestaurants(mockRestaurants)
    setLoading(false)
  }, [])

  useEffect(() => {
    let filtered = restaurants.filter(restaurant => {
      const matchesSearch = restaurant.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                           restaurant.cuisine_type.toLowerCase().includes(searchQuery.toLowerCase())
      const matchesCuisine = selectedCuisine === 'all' || restaurant.cuisine_type === selectedCuisine
      return matchesSearch && matchesCuisine && restaurant.is_active
    })

    // Sort restaurants
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'rating':
          return b.rating - a.rating
        case 'delivery_time':
          return a.delivery_time_min - b.delivery_time_min
        case 'delivery_fee':
          return a.delivery_fee - b.delivery_fee
        case 'name':
          return a.name.localeCompare(b.name)
        default:
          return 0
      }
    })

    // Put sponsored restaurants first
    filtered.sort((a, b) => {
      if (a.is_sponsored && !b.is_sponsored) return -1
      if (!a.is_sponsored && b.is_sponsored) return 1
      return 0
    })

    setFilteredRestaurants(filtered)
  }, [restaurants, searchQuery, selectedCuisine, sortBy])

  const cuisineTypes = ['all', ...Array.from(new Set(restaurants.map(r => r.cuisine_type)))]

  const t = (en: string, sw: string) => language === 'sw' ? sw : en

  if (loading) {
    return (
      <div className="min-h-screen bg-background">
        <Navigation />
        <div className="flex items-center justify-center h-64">
          <div className="text-lg">{t('Loading restaurants...', 'Inapakia migahawa...')}</div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Navigation />
      
      {/* Header */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">
            {t('All Restaurants', 'Migahawa Yote')}
          </h1>
          <p className="text-gray-600">
            {t('Discover amazing restaurants and order your favorite food', 'Gundua migahawa ya ajabu na uagize chakula chako unachokipenda')}
          </p>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex flex-col md:flex-row gap-4 items-center">
            <div className="flex-1">
              <Input
                type="search"
                placeholder={t('Search restaurants or cuisines...', 'Tafuta migahawa au aina za chakula...')}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="max-w-md"
              />
            </div>
            <div className="flex gap-4">
              <Select value={selectedCuisine} onValueChange={setSelectedCuisine}>
                <SelectTrigger className="w-40">
                  <SelectValue placeholder={t('Cuisine', 'Aina ya Chakula')} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">{t('All Cuisines', 'Aina Zote')}</SelectItem>
                  {cuisineTypes.slice(1).map(cuisine => (
                    <SelectItem key={cuisine} value={cuisine}>{cuisine}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Select value={sortBy} onValueChange={setSortBy}>
                <SelectTrigger className="w-40">
                  <SelectValue placeholder={t('Sort by', 'Panga kwa')} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="rating">{t('Rating', 'Ukadiriaji')}</SelectItem>
                  <SelectItem value="delivery_time">{t('Delivery Time', 'Muda wa Utoaji')}</SelectItem>
                  <SelectItem value="delivery_fee">{t('Delivery Fee', 'Ada ya Utoaji')}</SelectItem>
                  <SelectItem value="name">{t('Name', 'Jina')}</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </div>
      </div>

      {/* Restaurant Grid */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {filteredRestaurants.map((restaurant) => (
            <Link key={restaurant.id} href={`/restaurants/${restaurant.id}`}>
              <Card className="overflow-hidden group cursor-pointer transition-all hover:shadow-lg hover:scale-[1.02]">
                <div className="relative h-48">
                  <img
                    src={restaurant.image_url}
                    alt={restaurant.name}
                    className="w-full h-full object-cover"
                  />
                  {restaurant.is_sponsored && (
                    <span className="absolute top-2 right-2 bg-[#E32B44] text-white text-xs px-2 py-1 rounded-full font-medium">
                      {t('Sponsored', 'Imedhaminiwa')}
                    </span>
                  )}
                </div>
                <div className="p-4">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="font-semibold group-hover:text-[#E32B44] transition-colors">
                      {restaurant.name}
                    </h3>
                    <div className="flex items-center">
                      <span className="text-[#E32B44]">â</span>
                      <span className="ml-1 font-medium">{restaurant.rating}</span>
                      <span className="text-gray-500 text-sm ml-1">({restaurant.review_count})</span>
                    </div>
                  </div>
                  <p className="text-gray-600 text-sm mb-3 line-clamp-2">{restaurant.description}</p>
                  <div className="text-gray-500 text-sm space-y-1">
                    <div className="flex items-center justify-between">
                      <span className="px-2 py-1 bg-gray-100 rounded-full text-xs font-medium">
                        {restaurant.cuisine_type}
                      </span>
                      <span>{restaurant.delivery_time_min}-{restaurant.delivery_time_max} min</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span>{t('Delivery', 'Utoaji')}: ${restaurant.delivery_fee.toFixed(2)}</span>
                      <span>{t('Min order', 'Agizo la chini')}: ${restaurant.minimum_order.toFixed(2)}</span>
                    </div>
                  </div>
                </div>
              </Card>
            </Link>
          ))}
        </div>

        {filteredRestaurants.length === 0 && (
          <div className="text-center py-12">
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              {t('No restaurants found', 'Hakuna migahawa iliyopatikana')}
            </h3>
            <p className="text-gray-500">
              {t('Try adjusting your search or filters', 'Jaribu kubadilisha utafutaji au vichujio vyako')}
            </p>
          </div>
        )}
      </div>

      <Footer />
    </div>
  )
}
